div.place{
    position: relative;
    left: 35%;
    background: rgb(2, 115, 221);
    border: gold 3px solid;
    border-radius: 40px;
    width: 30%;
    height: 300px;
    margin-top: 100px;
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: right;
}
h3.reg{
    font-family: "Caladea";
    color: gold;
    margin-top: 20px;
    margin-bottom: 10px;
}
div.butDiv{
    position: relative;
    top: 20px;
}
button.but{
    font-family: "Trade Winds";
    color:gold;
    background: rgb(2, 115, 221);
    border: gold 3px solid;
    border-radius: 5px;
}
button.but:hover{
    background: rgb(16, 139, 255) ;
    border: 3px solid gold;
}